import React, { Component } from 'react';
import {base,ajax } from 'nc-lightapp-front';
let {NCCol,NCRow,NCTree,NCRadio} = base;
class OptionSelect extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedValue:'assign'
        };
    }
    onCheckFreeze(value){
        debugger;
        this.setState({
            selectedValue:value
        });
    }
    setIsAssign = (isAssign)=>{
        this.setState({
            selectedValue:isAssign
        })
    }
    /**
     * 是否分配
     * @returns {boolean}
     */
    getIsAssign = ()=>{
        return this.state.selectedValue == 'assign'?true:false;
    }
    render() {
        return (
            <NCRow md={12} xs={12} sm={12}>
                <NCCol md={12} xs={12} sm={12}>
                    <div style={{height:'400px', overflow:'scroll'}}>
                        <NCRadio.NCRadioGroup
                            name="freeSum"
                            selectedValue={this.state.selectedValue}
                            onChange={this.onCheckFreeze.bind(this)}>
                            <NCRadio value="assign">{this.props.Lang['10140SUG-000030']}</NCRadio>
                            <NCRadio value="cancelAssign">{this.props.Lang['10140SUG-000031']}</NCRadio>
                        </NCRadio.NCRadioGroup>
                    </div>
                </NCCol>
            </NCRow>
        )
    }
}
export default OptionSelect;
